<script>
  // import { beforeUpdate, afterUpdate } from "svelte";
  import ProductCard from "./components/ProductCard.svelte";
  import ProductsRenderer from "./components/ProductsRenderer.svelte";
  import ProductCategory from "./components/ProductCategory.svelte";
  import CategoryRenderer from "./components/CategoryRenderer.svelte";
  import BillItem from "./components/BillItem.svelte";
  import BillRenderer from "./components/BillRenderer.svelte";

  //variables
  let currentCategoryKey = 0;
  let currentProductKey = 0;
  let billTotal = 0;

  let productLookupDoulicationFlag = -1;
  //array of all bill's details
  let billDetails = [];

  //// Bill management Routines
  const blink = () => {
    let ele = document.querySelector("#q" + productLookupDoulicationFlag);
    // alert(ele);

    let bgStyel = ele.style.backgroundColor;
    ele.style.backgroundColor = "yellow";
    setTimeout(() => {
      ele.style.backgroundColor = bgStyel;
    }, 110);
  };

  const clacTotal = () => {
    let t = 0;
    billDetails.forEach(item => {
      t += parseFloat(item.itemQuantity) * parseFloat(item.itemPrice);
    });
    return t;
  };
  const delBillItem = index => {
    if (confirm("ARE YOU SURE ??")) {
      billDetails = billDetails.filter((ele, i) => i != index);
    }
    //billDetails = billDetails;
    // productLookupDoulicationFlag = -1;
    reArrangeBillKeys();
    billTotal = clacTotal();
  };

  const reArrangeBillKeys = () => {
    billDetails.forEach((item, index) => {
      item.key = index;
    });
  };

  const updateBillValues = index => {
    let qInputs = document.querySelectorAll("#right input");
    let ele = qInputs[index];
    let value = ele.value;
    billDetails[index].itemQuantity = value;
  };
  const onBillItemUpdate = e => {
    let index = e.detail.key;
    updateBillValues(index);
    billTotal = clacTotal();
  };
  const onBillItemDelete = e => {
    let index = e.detail.key;
    delBillItem(index);
  };

  const addBillItem = (catkey, prodkey) => {
    let obj = {
      key: billDetails.length,
      catName: categories[catkey].name,
      itemImgPath: categories[catkey].products[prodkey].imgPath,
      itemName: categories[catkey].products[prodkey].name,
      itemPrice: categories[catkey].products[prodkey].price,
      itemQuantity: 1 //default value
    };
    // billDetails.push(obj);
    // billDetails = billDetails;

    //instead do list assaignment
    billDetails = [...billDetails, obj];
  };

  ///////////////////////////////////////////////////////////////////

  ////////    products and category Routines     /////////////////////

  //look up product by name in billDetails array and return
  // its index or -1
  const productLookUp = prodname => {
    let index = -1;
    for (var i = 0; i < billDetails.length; i++) {
      if (billDetails[i].itemName === prodname) {
        index = billDetails[i].key;
        break;
      }
    }
    return index;
  };

  const onCategorySelected = e => {
    currentCategoryKey = parseInt(e.detail.key);
  };

  const onProductSelected = e => {
    currentProductKey = parseInt(e.detail.key);
    // if (billDetails[currentProductKey] !== undefined)
    productLookupDoulicationFlag = productLookUp(
      categories[currentCategoryKey].products[currentProductKey].name
    );
    //alert(productLookupDoulicationFlag);
    if (productLookupDoulicationFlag == -1) {
      addBillItem(currentCategoryKey, currentProductKey);
      billTotal = clacTotal();
    } else {
      //dont add the item again instead updte its count by 1
      let qInputs = document.querySelectorAll("#right input");
      let ele = qInputs[productLookupDoulicationFlag];
      let value = parseFloat(ele.value) + 1;
      blink();
      billDetails[productLookupDoulicationFlag].itemQuantity = value;
      ele.value = value;
      billTotal = clacTotal();
    }

    //console.log(currentCategoryKey, currentProductKey, billDetails);
  };

  ////////////////////////////////////////////////////////////////////

  /////////////////////////     sample back-end data /////////////////

  let categories = [
    {
      name: "sandwitches",
      count: "2",
      products: [
        {
          name: "Burger",
          price: "100",
          desc: "",
          imgPath: "sand-burger.jpg"
        },
        {
          name: "Flafil",
          price: "80",
          desc: "",
          imgPath: "sand-flfil.jpg"
        }
      ]
    },
    {
      name: "pizza",
      count: "5",
      products: [
        {
          name: "pepperoni",
          price: "300",
          desc: "",
          imgPath: "pizza-pepperoni.jpg"
        },
        {
          name: "cheese",
          price: "300",
          desc: "",
          imgPath: "pizza-cheese.jpg"
        },
        {
          name: "chicken",
          price: "300",
          desc: "",
          imgPath: "pizza-chicken.jpg"
        },
        {
          name: "margherita",
          price: "560",
          desc: "",
          imgPath: "pizza-margherita.jpg"
        },
        {
          name: "mashroom ",
          price: "300",
          desc: "",
          imgPath: "pizza-mashroom.jpg"
        }
      ]
    },
    {
      name: "Asida",
      count: "3",
      products: [
        {
          name: "Rybe",
          price: "120",
          desc: "",
          imgPath: "asida-rybe.jpg"
        },
        {
          name: "Tagalia",
          price: "120",
          desc: "",
          imgPath: "asida-taglia.jpg"
        },
        {
          name: "Wekab",
          price: "140",
          desc: "",
          imgPath: "asida-wekab.jpg"
        }
      ]
    },
    {
      name: "Juice",
      count: "4",
      products: [
        {
          name: "Orange",
          price: "90",
          desc: "",
          imgPath: "juice-orange.jpg"
        },
        {
          name: "Mango",
          price: "90",
          desc: "",
          imgPath: "juice-mango.jpg"
        },
        {
          name: "Guava",
          price: "100",
          desc: "",
          imgPath: "juice-guava.jpg"
        },
        {
          name: "Mixed",
          price: "140",
          desc: "",
          imgPath: "juice-mixed.jpg"
        }
      ]
    }
  ];
</script>

