<div id="content">
  <div id="header" />
  <div id="left">
    <div id="products-categories">

      <CategoryRenderer
        catList={categories}
        on:onCategorySelected={onCategorySelected} />
    </div>

    <div id="products-details">
      <ProductsRenderer
        prodList={categories[currentCategoryKey].products}
        on:onProductSelected={onProductSelected} />
    </div>
  </div>
  <div id="right">
    <div id="products-counter-details">
      <BillRenderer
        billItemsList={billDetails}
        on:BillItemUpdate={onBillItemUpdate}
        on:BillItemDelete={onBillItemDelete} />
      <span id="total">Total = {billTotal}</span>
    </div>

  </div>

